class InfiniteSlider extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            allData: null,
            initAllData: null,
            isZoomed: false,
            scrollDirection: null
        }

        this.counter = 0;

        this.initPageScroll = 0;

        this.interval = null;

        this.direction = null;

        this.displayData = [];

        this.windowHeight = null;

        this.windowWidth = null;

        this.touchMoved = null;

        this.handleSliderScroll = _.throttle(this.handleSliderScroll.bind(this), 200);

        this.handleThrottledScroll = _.throttle(this.handleThrottledScroll.bind(this), 300);

        this.mouse = null;
        
        // this.zoomOut = _.throttle(this.zoomOut.bind(this), 200);
        
        

    }

    componentWillMount(){

    }

    componentDidMount(){

        this.mouse = document.querySelector('.mouse');


        this.setState({
            allData: this.props.data,
            initAllData: this.props.data,
        });




        this.windowHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);

        this.windowWidth = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);


        // window.addEventListener('resize', () => {
        //     this.windowHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);
        // });

        
        setTimeout(function() {

            this.flkty.resize();

            if(window.innerWidth >= 992) {

                document.querySelector('.infinite-scroll').addEventListener('scroll',this.handleSliderScroll);
    
                this.flkty.on('dragMove', function(mouseVector) {
                 
                    const x = mouseVector.clientX;
                    const y = mouseVector.clientY;
                    const mouse = document.querySelector('.mouse');
              
              
                    TweenLite.to(mouse, 0, {x: x, y: y});
    
                });
            }

          }.bind(this),250);


    }

    handleSliderScroll() {

        let initPageScroll = this.initPageScroll;
        let menuScroll = document.querySelector('.flickity-slider');
        let window = document.querySelector('.infinite-scroll');

        let speed = Math.random() * 1;
        
  
        if(window) {
  
          if(window.scrollTop >= initPageScroll) {

            if(this.props.number % 2 == 0) {
                
                this.flkty.applyForce(speed);
                this.flkty.startAnimation();
                this.flkty.dragEnd();

                this.setState({
                    scrollDirection: 'left'
                })

            } else {
                this.flkty.applyForce(-speed);
                this.flkty.startAnimation();
                this.flkty.dragEnd();

                this.setState({
                    scrollDirection: 'right'
                })

            }
    
          } else if (window.scrollTop <= initPageScroll ) {

            if(this.props.number % 2 == 0) {

                this.flkty.applyForce(-speed);
                this.flkty.startAnimation();
                this.flkty.dragEnd();

                this.setState({
                    scrollDirection: 'right'
                })


            } else {

                this.flkty.applyForce(speed);
                this.flkty.startAnimation();
                this.flkty.dragEnd();

                this.setState({
                    scrollDirection: 'left'
                })

            }

    
          }
    
          this.initPageScroll = window.scrollTop;

          setTimeout(()=>{
              this.setState({
                  scrollDirection: null
              })
          },1000);

        }

    }


    handleScroll = () => {
        this.handleThrottledScroll();
    }

    handleThrottledScroll = () => {

       

    }

    zoomIn = () => {

        if(this.windowWidth >= 992) {

            document.querySelector(this.props.containerClassName).addEventListener('scroll', this.zoomOut);

        }
    

        // console.log(document.querySelector(this.props.containerClassName));

        // window.addEventListener('scroll', this.zoomOut);

        this.setState({
            isZoomed: true
        });
        
      }
    
    zoomOut = () => {

        document.querySelector(this.props.containerClassName).removeEventListener('scroll', this.zoomOut);
        

        // window.removeEventListener('scroll', this.zoomOut);

            this.setState({
                isZoomed: false
            });

      };

    toggleZoom = () => (this.state.isZoomed ? this.zoomOut() : this.zoomIn());

    mouseEnter = () => {

        if(this.windowWidth <= 992) {
            return
        }

        TweenLite.to('.mouse-inner', 0.2, {scale: 1});
        TweenLite.to('.mouse', 0.2, {left: '-48px', fill: 'white'});
        TweenLite.to('.cls-2', 0.2, {stroke: 'red'});


        var circle = document.getElementById("Ebene_2");
        var tl = new TimelineLite();

        tl.to(circle, 0.5, {rotation:-360, scale:1, stroke: 'red'})
          .to(circle, 0.5, {rotation:360, scale: 1, ease:Power1.easeInOut})
          .to(circle, 0.5, {scale: 0.9});

        let startTime = new Date().getTime();
        let currentTime;


        this.interval = setInterval( () => {
            
                let currentTime = new Date().getTime();
                console.log(currentTime);
    
                if(currentTime - startTime > 1000) {
                    this.toggleZoom();
                    clearInterval(this.interval);
                    
                }
    
            },100);

    }

    mouseLeave = () => {

        clearInterval(this.interval);

        TweenLite.to('.mouse-inner', 0.2, {scale: 0.3, stroke: 'none'});
        TweenLite.to('.mouse', 0.2, {left: '-18px'});
        TweenLite.to('.cls-2', 0.2, {stroke: 'none'});

    }

    _onTouchStart = (e) => {

        this.touchMoved = false;

    }

    _onTouchMove = (e) => {

        this.touchMoved = true;

    }

    _onTouchEnd = (e) => {

        if(this.touchMoved != true ) {

            if(this.state.isZoomed) {
                return
            }

            this.toggleZoom();

        }

    }

    


    render(){

        const {allData, initAllData} = this.state;

        const flickityOptions = {
            
            prevNextButtons: false,   
            pageDots: false,
            freeScroll: true,
            wrapAround: true,
            imagesLoaded: true,
            freeScrollFriction: 0.03,
        }

       
        return (
            <PerfectScrollbarHolder data-scene className={this.state.isZoomed ? 'open-holder' : 'closed-holder' } onTouchStart={this._onTouchStart} onTouchEnd={this._onTouchEnd} onTouchMove={this._onTouchMove}  onMouseEnter={this.mouseEnter} onMouseLeave={this.mouseLeave}>
            <StyledZoomDiv windowHeight={this.windowHeight} windowWidth={this.windowWidth} pose={this.state.isZoomed ? 'open' : 'closed' }>

            <StyledDescription pose={this.state.isZoomed ? 'open' : 'closed' }>
                 <Title>
                    Supersplash
                </Title> 

                <Year>
                     2019
                </Year>
            </StyledDescription>

            { allData != null ?

                    <Flickity
                    flickityRef={c => this.flkty = c}
                    ref={ div => this.sliderRef = div }
                    className={this.state.scrollDirection ? (this.state.scrollDirection == 'right' ? 'infinite-slider scrolling-right' : 'infinite-slider scrolling-left') : 'infinite-slider' } // default ''
                    elementType={'div'} // default 'div'
                    options={flickityOptions} // takes flickity options {}
                    // disableImagesLoaded={true} // default false
                    // reloadOnUpdate // default false
                    >
                    {
                        allData.images.map((item, index) => { 
                        
                            return (
                            // <StyledImg  objectFit="cover" objectPosition="50% 50%" fluid={item.childImageSharp.fluid}/>
                            <img className="slider-image" ref={img => this.imageRef = img} key={index} src={item.childImageSharp.fluid.src}/>
                            )
                        })
                    }

                    </Flickity>

                    :

                    <div></div>





            }
            <StyledFrame backgroundColor={this.props.backgroundColor} pose={this.state.isZoomed ? 'open' : 'closed' }/>

            </StyledZoomDiv>
            <StyledCloseButton onClick={this.toggleZoom}  pose={this.state.isZoomed ? 'open' : 'closed' }>CLOSE</StyledCloseButton>
            </PerfectScrollbarHolder>
        )
    }
}

export default InfiniteSlider
